<template>
  <div class="register">
    <el-container class="register-wrap">
      <el-header class="register-head" style="height:38px;background-color:rgba(255,255,255,0.6)"></el-header>
      <el-main class="register-main">
        <div class="register-main-content">
          <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="学生用户" name="student">
              <el-form :model="studentForm"
                       status-icon
                       :rules="studentRules"
                       ref="studentForm"
                       label-width="100px"
                       class="demo-ruleForm">
                <el-form-item label="姓名" prop="username">
                  <el-input v-model="studentForm.username"></el-input>
                </el-form-item>
                <el-form-item label="账号" prop="number">
                  <el-input v-model="studentForm.number"></el-input>
                </el-form-item>
                <el-form-item label="学校" prop="school">
                  <el-select v-model="studentForm.school" filterable placeholder="请选择">
                    <el-option
                      v-for="item in studentOptions"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="专业" prop="major">
                  <el-input v-model="studentForm.major"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="pass">
                  <el-input type="password"
                            v-model="studentForm.pass"
                            autocomplete="off">
                  </el-input>
                </el-form-item>
                <el-form-item label="确认密码" prop="checkPass">
                  <el-input type="password"
                            v-model="studentForm.checkPass"
                            autocomplete="off">
                  </el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary"
                             @click="submitForm('studentForm')">注册</el-button>
                  <el-button @click="resetForm('studentForm')">重置</el-button>
                </el-form-item>
              </el-form>
            </el-tab-pane>
            <el-tab-pane label="教师用户" name="teacher">
              <el-form :model="teacherForm"
                       status-icon
                       :rules="teacherRules"
                       ref="teacherForm"
                       label-width="100px"
                       class="demo-ruleForm">
                <el-form-item label="姓名" prop="username">
                  <el-input v-model="teacherForm.username"></el-input>
                </el-form-item>
                <el-form-item label="账号" prop="number">
                  <el-input v-model="teacherForm.number"></el-input>
                </el-form-item>
                <el-form-item label="学校" prop="school">
                  <el-select v-model="teacherForm.school" filterable placeholder="请选择">
                    <el-option
                      v-for="item in teacherOptions"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="专业" prop="major">
                  <el-input v-model="teacherForm.major"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="pass">
                  <el-input type="password"
                            v-model="teacherForm.pass"
                            autocomplete="off">
                  </el-input>
                </el-form-item>
                <el-form-item label="确认密码" prop="checkPass">
                  <el-input type="password"
                            v-model="teacherForm.checkPass"
                            autocomplete="off">
                  </el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary"
                             @click="submitForm('teacherForm')">注册</el-button>
                  <el-button @click="resetForm('teacherForm')">重置</el-button>
                </el-form-item>
              </el-form>
            </el-tab-pane>
            <el-tab-pane label="管理员用户" name="admin">
              <el-form :model="adminForm"
                       status-icon
                       :rules="adminRules"
                       ref="adminForm"
                       label-width="100px"
                       class="demo-ruleForm">
                <el-form-item label="学校代码" prop="schoolNumber">
                  <el-input v-model="adminForm.schoolNumber"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="pass">
                  <el-input type="password"
                            v-model="adminForm.pass"
                            autocomplete="off">
                  </el-input>
                </el-form-item>
                <el-form-item label="确认密码" prop="checkPass">
                  <el-input type="password"
                            v-model="adminForm.checkPass"
                            autocomplete="off">
                  </el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary"
                             @click="submitForm('adminForm')">注册</el-button>
                  <el-button @click="resetForm('adminForm')">重置</el-button>
                </el-form-item>
              </el-form>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-main>
      <e-footer class="register-foot"></e-footer>
    </el-container>
  </div>
</template>

<script>
  export default {
      data(){
          const validatePass = (rule, value, callback) => {
              if (value === '') {
                  callback(new Error('请输入密码'))
              } else {
                  let patrn = /(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[^0-9]).{8,30}/
                  if (!patrn.test(value)) {
                      callback(new Error('包含大小写字母、数字，8至30个字符'))
                  }
                  if (this.pwdForm.newPwdCheck !== '') {
                      // 对第一次输入的新密码单独验证
                      // console.log(this.$refs.pwdForm)
                      this.$refs.pwdForm.validateField('newPwdCheck')
                  }
                  callback()
              }
          }
          const validatePassCheck = (rule, value, callback) => {
              let patrn = /(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[^0-9]).{8,30}/
              if (value === '') {
                  callback(new Error('请再次输入密码'))
              } else if (value !== this.studentForm.pass) {
                  callback(new Error('两次输入密码不一致!'))
              } else if (!patrn.test(value)) {
                  callback(new Error('包含大小写字母、数字，8至30个字符 '))
              } else {
                  callback()
              }
          }
          return {
              activeName:'student',
              studentForm:{
                username:'',
                number:'',
                school:'',
                major:'',
                pass:'',
                checkPass:''
              },
              studentRules:{
                  username: [
                      { required: true, message: '姓名不能为空', trigger: 'blur' }
                  ],
                  number: [
                      { required: true, message: '账号不能为空', trigger: 'blur' }
                  ],
                  school: [
                      { required: true, message: '学校不能为空', trigger: 'blur' }
                  ],
                  major: [
                      { required: true, message: '专业不能为空', trigger: 'blur' }
                  ],
                  pass: [
                      { required: true, message: '密码不能为空', trigger: 'blur' },
                      { validator: validatePass, trigger: 'blur' }
                  ],
                  checkPass: [
                      { required: true, message: '请再次确认密码', trigger: 'blur' },
                      { validator: validatePassCheck, trigger: 'blur' }
                  ],

              },
              studentOptions:[],
              teacherForm:{
                  username:'',
                  number:'',
                  school:'',
                  major:'',
                  pass:'',
                  checkPass:''
              },
              teacherRules:{
                  username: [
                      { required: true, message: '姓名不能为空', trigger: 'blur' }
                  ],
                  number: [
                      { required: true, message: '账号不能为空', trigger: 'blur' }
                  ],
                  school: [
                      { required: true, message: '学校不能为空', trigger: 'blur' }
                  ],
                  major: [
                      { required: true, message: '专业不能为空', trigger: 'blur' }
                  ],
                  pass: [
                      { required: true, message: '密码不能为空', trigger: 'blur' },
                      { validator: validatePass, trigger: 'blur' }
                  ],
                  checkPass: [
                      { required: true, message: '请再次确认密码', trigger: 'blur' },
                      { validator: validatePassCheck, trigger: 'blur' }
                  ],
              },
              teacherOptions:[],
              adminForm:{
                  schoolNumber:'',
                  pass:'',
                  checkPass:''
              },
              adminRules:{
                  schoolNumber: [
                      { required: true, message: '学校代码不能为空', trigger: 'blur' }
                  ],
                  pass: [
                      { required: true, message: '密码不能为空', trigger: 'blur' },
                      { validator: validatePass, trigger: 'blur' }
                  ],
                  checkPass: [
                      { required: true, message: '请再次确认密码', trigger: 'blur' },
                      { validator: validatePassCheck, trigger: 'blur' }
                  ],
              },


          }
      },
      methods:{
          submitForm(name){
            console.log(name);

          },
          resetForm(name){
              this.$refs[name].resetFields()
          },
          handleClick(){

          }
      }
  }

</script>

<style lang="less">
.register{
  height: 100%;
  .register-wrap{
    height: 100%;
    background-image: url("../assets/img/register.jpeg");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    .register-head{
    }
    .register-main{
      width: 40%;
      margin: auto;
      margin-top: 30px;
      overflow: hidden;
      .register-main-content{
        padding: 20px;
        height: 470px;
        color: #fff;
        min-width: 450px;
        border-radius: 5px;
        background-color: rgba(255,255,255,.1);
        .el-tabs__item{
          color: #fff;
          font-weight: bolder;
        }
        .el-form-item__label{
          color: #fff;
          font-weight: bolder;
        }
        .el-input{
          width: 80%;
          min-width: 80%;
        }
        .el-input__inner{
          height: 35px;
          line-height: 35px;
          background-color: transparent;
          color: #fff;
        }
        .el-form-item__content{
        }
      }
    }
    .register-foot{

    }

  }
}
</style>





