<template>
  <div class="resource-list-wrap">
    <ul class="resource-list">
      <li>
<!--        列表内容上半部分-->
        <el-row class="list-content"
                type="flex"
                justify="space-around">
          <el-col class="res-img" :span=4>
            <img src="../../assets/img/Koala.jpg" alt="">
          </el-col>
          <el-col class="res-content" :span=12>
            电费卡吉岛了
          </el-col>
          <el-col class="author" :span=4>
            <div class="author-icon">
              <img src="../../assets/img/Koala.jpg" alt="">
            </div>
            <div class="name">balalala</div>
          </el-col>
        </el-row>
<!--        列表操作部分内容-->
        <el-row class="operate" type="flex" justify="space-between">
          <el-col class="left-con">
            <el-col class="download-number" :span=6>
              下载量:<span>5</span>
            </el-col>
            <el-col class="subject" :span=6 :pull=1>
              <el-tag type="success" size="mini" style="border-radius: 50%;padding: 5px;">艺术学</el-tag>
            </el-col>
          </el-col>
          <el-col class="right-con" :push=6>
            <el-col class="time" :span=8>
              2019年11月21日
            </el-col>
            <el-col class="download" :span=2>
              <i class="el-icon-download"></i>
            </el-col>
            <el-col class="comment" :span=2>
              <i class="el-icon-chat-dot-round" @click=handleCommon></i>
            </el-col>
          </el-col>
        </el-row>
        <el-input
          type="textarea"
          :rows="2"
          placeholder="留下你想说的fa..."
          v-model="textarea">
        </el-input>
        <el-row class="buttons">
          <el-button size="mini" round @click="handleSubmit">提交</el-button>
          <el-button size="mini" round @click="handleCancel">取消</el-button>
        </el-row>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
      data(){
        return {
            textarea:''
        }
      },
      methods:{
          //点击评论按钮
          handleCommon:function(){

          },
          //提交评论
          handleSubmit:function(){
             
          },
          //取消评论
          handleCancel:function(){
            this.textarea = '';
          }
      },
      components:{
      }

  }
</script>

<style lang="less">
@import '../../assets/css/resourceList/resourceList.less';
</style>



