<template>
  <div class="wrapper">
    <div class="wrap" >
      <topBar/>
      <el-row class="main-content">
        <Menu/>
        <SearchBar/>
        <ResourceList/>
      </el-row>
    </div>
  </div>
</template>

<script>
import topBar from '@/components/header/topBar.vue';
import Menu from '@/components/menu/menuList.vue';
import SearchBar from '@/components/search/searchBar.vue';
import FooterBar from '@/components/footer/footerBar.vue';
import ResourceList from '@/components/resourceList/resourceList.vue';

export default {
  components:{
      topBar,
      Menu,
      SearchBar,
      FooterBar,
      ResourceList
  }
}
</script>

<style scoped lang="less">
.wrapper{
  .wrap{
    .header-bar{
      padding: 0;
      height: 40px;
    }
    .main-content{
      position: relative;
      padding: 0px;
      min-width:1190px;
    }
  }

}
</style>



