import Vue from 'vue'
import Router from 'vue-router'
import Register from '@/page/register.vue'
import Login from '@/page/login.vue'
import Index from '@/page/index.vue'
import Upload from '@/page/upload.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      name:'register',
      component: Register
    },
    {
      path:'/index',
      name:'index',
      component:Index
    },
    {
      path:'/login',
      name:'login',
      component:Login
    },
    {
      path:'/upload',
      name:'upload',
      component:Upload
    }
  ]
})



