<template>
  <el-form :model="fileForm"
           status-icon
           :rules="fileRules"
           ref="fileForm"
           label-width="100px"
           class="file-form">
    <el-form-item label="选择学校">
      <el-select v-model="fileForm.school"
                 filterable
                 placeholder="请选择">
        <el-option
          v-for="item in schoolOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="选择学科">
      <el-select v-model="fileForm.subject"
                 filterable
                 placeholder="请选择">
        <el-option
          v-for="item in schoolOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('fileForm')">提交</el-button>
      <el-button @click="resetForm('fileForm')">重置</el-button>
    </el-form-item>
  </el-form>
</template>
<script>
    export default {
        data() {
            return {
                fileForm: {
                  school:'',
                  subject:''
                },
                fileRules:{

                },
                schoolOptions:[
                    {
                        value:'052160',
                        label:'河北经贸大学'
                    }
                ]
            };
        },
        methods: {
            submitForm(formName) {
            },
            resetForm(formName) {
            }
        }
    }
</script>



